import React from 'react';
import logo from './logo.svg';
import set3Images from './set3Images';
import set3ProbTable from './set3ProbabilityTable';
import './App.css';

const emptyImg = set3Images[0].src;
let money = 50;
let level = 1;

function App() {
  return (
    <div className="App">
      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
        <p>
          Edit <code>src/App.js</code> and save to reload.
        </p>
        <a
          className="App-link"
          href="https://reactjs.org"
          target="_blank"
          rel="noopener noreferrer"
        >
          Learn React
        </a>
      </header>
      <h1>Hi</h1>

      <ChampHand/>
    </div>
  );
}



class ChampHand extends React.Component {
  constructor(props) {
    super(props);
    this.state = {tiles:[
      { id: 1, src: '/assets/set3/ahri.png', title: 'ahri', cost: 2, },
      { id: 2, src: '/assets/set3/annie.png', title: 'annie', cost: 2 },
      { id: 3, src: '/assets/set3/ashe.png', title: 'ashe', cost: 3 },
      { id: 4, src: '/assets/set3/aurelion-sol.png', title: 'aurelion-sol', cost: 5 },
      { id: 5, src: '/assets/set3/blitzcrank.png', title: 'blitzcrank', cost: 2 },
    ]};
    this.randomizeChamps = this.randomizeChamps.bind(this);

  }


   randomizeChamps(){
     //get the correct probabilty spread;
     let probSpread = set3ProbTable.filter((row)=>{
       return row.level === level;
     })[0];

     let probSpreadValues = Object.values(probSpread);

    //chooses 5 champs based on table probabilities and level
    let randomChamps = [];
    for(let i = 0; i < 5; i++){
      //roll from 1-100
      let randomNum = Math.floor(Math.random() * 100) + 1;
      let champCost = 0;
      //convert probspread to array to roll the random number through the prob probSpread
      for(let i = 1; i <= 5; i++){
        if(randomNum - probSpreadValues[i] <= 0){
          champCost = i;
          break;
        }else{
          randomNum -= probSpreadValues[i];
        }
      }
      //filter all the correct cost champs
      let set3CorrectCostChamps = set3Images.filter(champ =>
        champ.cost === champCost
      );

      randomChamps.push(set3CorrectCostChamps[Math.floor(Math.random() * set3CorrectCostChamps.length)]);
    }
    console.log(randomChamps);
    this.setState({tiles: randomChamps});
    // this.setState({tiles: [{ id: 1, src: '/assets/set3/ahri.png', title: 'ahri', cost: 2, }]});
    console.log(this.state.tiles);
  }

  componentDidMount(){
    //roll the first random randomChamps
    // alert("Hey");
    // this.randomizeChamps();
  }

  render() {
    return (
      <div>
        <RerollButton onReroll={this.randomizeChamps}/>
        {this.state.tiles.map((tile, i) => (
          <ChampTile key={i} src={process.env.PUBLIC_URL + tile.src} alt={tile.title} cost={tile.cost}/>
        ))}

      </div>
    )
  }
}



class RerollButton extends React.Component {
  constructor(props){
    super(props);
    this.state = {};
    this.reroll = this.reroll.bind(this);
  }

  reroll() {
    if(money >= 2){
      console.log("rerolling");
      money -= 2;
      this.props.onReroll();
    }
  }

  render() {
    return (
      <button onClick={this.reroll}>Reroll</button>
    )
  }
}

class ChampTile extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      hasChamp: true
    };
    this.buyChamp = this.buyChamp.bind(this);
  }

  buyChamp() {
    if(this.state.hasChamp && money >= this.props.cost){
      this.setState({hasChamp: false});
      money -= this.props.cost;
    }
  }

  componentWillReceiveProps({props}){
    this.setState({hasChamp: true});
  }

  render() {
    return (
      <button><img src={this.state.hasChamp ? this.props.src : process.env.PUBLIC_URL + emptyImg} alt={this.props.alt} onClick={this.buyChamp}/></button>
    )
  }
}

export default App;
